
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {getCookie} from '@/tool/common.js';
import Drive from '@/tool/classFactory/drive.js';
export default {
directives: {
TransferDom
},
components: {
Confirm,
Group,
Cell,
Popup,
XButton,
XSwitch,
CheckIcon,
Checklist,
Toast
},
data () {
return {
clientHeight:'',
tips: '技巧讲解',
answer: '浏览实际情况请绕道而行。',
showcancel: true,
cancel: '重听',
congfirm:'关闭',
show: false,
popup: false,
msg: 'Hello World!',
demo1: false,
demo2: true,
commonList: [ 'China', 'Japan', 'America' ],
radioValue: [],
radioOn:true,
params:{
'sessionId':'',
'carType':'',
'type':'',
},
questionList:[],
question:{},
questionIndex: 0,
count: 0,
questionType: '',
choosed: 3,
params02:{
'type':'2',
'sessionId':''
},
params03:{
'type':'2',
'index':'',
'qid':'',
'sessionId':''
},
params04:{
'mid':'',
'sessionId':''
},
examInfo:{

},
time:2700,
btntxt:'',
noFinish: 0,
chooseList:[],
finish:false,
redo:'',
audio: {
currentTime: 0,
maxTime: 0,
playing: false,
muted: false,
speed: 1,
waiting: true,
preload: 'auto'
},
controlList: {

noDownload: false,

noMuted: false,

noVolume: false,

noProcess: false,

onlyOnePlaying: false,

noSpeed: false
},

audioStyle: '1',
params05:{
'sessionId':''
},
automatic: false,
audioStyle: '1',
audioAdd: false,
music:'',
nextLoading: false,
showPositionValue: false,
toast: '',
middle:'middle',
examInfo:{
'endTimeLong':'',
'startTimeLong':'',
'sorce':''
}
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          
that.changeFixed(this.clientHeight)
},
created () {
this.params04.mid = this.params.mid = this.$route.query.mid || null
this.params.carType = this.$route.query.carType || null
this.params.type = this.$route.query.type || null
this.params05.sessionId = this.params04.sessionId = this.params03.sessionId = this.params02.sessionId = this.params.sessionId = getCookie('sessionId')
this.redo = this.$route.query.redo || null
if(this.params.type == '2'){
this.time = 1800

}
if(this.params.type == '1'){
this.time = 2700

}
if(this.redo == 1){
this.redoQuestionList()
}else {
this.getQuestionList()
}
this.getExamInfo()
this.timer()
},
methods: {
timer: function () {
if (this.time > 0) {
this.time--;

var m = '';
var s = '';
m = Math.floor(this.time/60%60);
m < 10&&(m='0'+m);
s = Math.floor(this.time%60);
s < 10&&(s='0'+s);
this.btntxt = m + ':' + s
setTimeout(this.timer, 1000);
} else {
this.time = 0;
this.disabled = false;
this.tips = '时间到'
this.answer = '45分钟考试时间到，请交卷。'
this.showcancel =  false
this.congfirm = '交卷'
this.finish = true

this.showPositionValue = true,
this.toast = '考试时间到',
this.handIn()
}
},
handIn (e){
const that = this;
Drive.prototype.handIn(that.params05).then(res => {
let list = res.data.data || {};
that.examInfo = list
console.log(that.examInfo)
that.$router.push('../result?type='+ 2  +'&&endTimeLong=' +  that.examInfo.endTimeLong + '&&startTimeLong=' + that.examInfo.startTimeLong + '&&sorce=' + that.examInfo.sorce)
});
},
changeFixed(clientHeight) {                        //动态修改样式
this.$refs.homePage.style.height = clientHeight + 'px';

},
getQuestionList (e){
const that = this;
Drive.prototype.getQuestionList(that.params).then(res => {
let list = res.data.data || [];
that.questionList = list.resultList
that.question = that.questionList[that.questionIndex]
that.count = list.count

that.setquestionType()
});
},
redoQuestionList (e){
const that = this;
Drive.prototype.redoQuestionList(that.params04).then(res => {
let list = res.data.data || [];
that.questionList = list.resultList
that.question = that.questionList[that.questionIndex]
that.count = list.count

that.setquestionType()
});
},
againMultipleselection(){
var str = '12;12;12;'
var array = str.split(";")

},
setquestionType(){
const that = this
for(var idx in that.question.optionList){
that.question.optionList[idx].key = that.question.optionList[idx].id
that.question.optionList[idx].value = that.question.optionList[idx].optionContent
}

if(that.question.type == 3){
if(that.question.isRight != 0){
for(var idx in that.question.optionList){
if(that.question.optionList[idx].id == that.question.chooseId){
that.choosed = idx
break
}
}
}else {
that.choosed = 3
}

}

if(that.question.type == 1){
if(that.question.isRight != 0){
for(var idx in that.question.optionList){
if(that.question.optionList[idx].id == that.question.chooseId){
that.radioValue.push(that.question.optionList[idx].id)
break
}
}
}else {
that.radioValue = []
}
}
if(that.question.type == 2){
if(that.question.isRight != 0){
that.radioValue = that.question.chooseId.split(';')






}else {
that.radioValue = []
}
}

if(that.question.type == 1){
that.questionType = '单选题'
}
if(that.question.type == 2){
that.questionType = '多选题'
}
if(that.question.type == 3){
that.questionType = '判断题'
}
},
getExamInfo (e){
const that = this;
Drive.prototype.getExamInfo(that.params02).then(res => {
let list = res.data.data || {};
that.examInfo = list

});
},
onQuestionTap (item,index){
const that = this
console.log(this.questionList)
that.radioOn = false
this.questionIndex = index
this.question = this.questionList[index]
that.setquestionType()
},
judgeQuestion (e){
const that = this;
Drive.prototype.judgeQuestion(that.params03).then(res => {
let list = res.data.data || {};
that.questionList = list.examDto.questionDtoList
that.choosed = 3
that.radioValue = []
if(!e){
++that.questionIndex
if(!this.audio.playing && !this.automatic){
this.nextLoading = false
this.playHelpMp3 = false
}
}
if(that.questionIndex == that.count){
--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.setquestionType()

that.showTips()
}else {
that.question = that.questionList[that.questionIndex]
that.setquestionType()

if(this.automatic){
that.nextLoading = true
}
that.music = this.question.mp3
}
});
},
onChooseTap (e){
const that = this;
that.choosed = e
that.params03.index = that.questionIndex;  
that.params03.qid = that.question.id;           
that.params03.optionId = that.question.optionList[e].id;      
if(this.question.isRight == 0){
this.judgeQuestion()
}else {
that.showPositionValue = true
that.toast = '模拟考试中不能对之前已答过的题重复答题'
}
},
change (e){
console.log('change')
const that = this
that.params03.index = that.questionIndex;  
that.params03.qid = that.question.id;           


if( that.radioValue.length != 0 && that.radioOn){
if(that.question.type == 1){
that.params03.optionId = that.radioValue[0]
if(that.question.isRight == 0){
this.judgeQuestion()
} else {

that.showPositionValue = true
that.toast = '模拟考试中不能对之前已答过的题重复答题'
}
}
if(that.question.type == 2){
if(that.question.isRight != 0){
that.showPositionValue = true
that.toast = '模拟考试中不能对之前已答过的题重复答题'
}
}
}else {
if(that.radioValue.length != 0){
that.radioOn = !that.radioOn
}
}

},
processMultipleselection(){
var str = ''
this.radioValue.forEach(item => {
console.log(item)
str += `${item}`;
str += ';';
});
return str.slice(0,-1);
},
onNextTap (){
const that = this























if(that.count > that.questionIndex+1){
that.question = that.questionList[that.questionIndex+1]
that.questionIndex++
that.setquestionType()
if(this.audio.playing){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
}else {
that.showTips()
}
},
onLastTap(){
const that = this























if(that.questionIndex > 0){




--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.radioOn = false
that.setquestionType()
if(this.audio.playing){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
}else {
console.log('first one')
}

},
onCheckTap(){
const that = this
that.popup = true
that.radioOn = false
this.getExamInfo()
},
showTips(){
const that = this
that.noFinish = 0
for(var idx in that.questionList){
if(that.questionList[idx].isRight == 0){
++that.noFinish
}
}
if(that.noFinish > 0){
var num = that.count - that.noFinish
that.tips = '您还有' + `${that.noFinish}` + '道没有完成'
that.answer = '是否确认交卷？'
that.cancel = '再想想'
that.congfirm= '立即交卷'
}else {




that.tips = ''
that.answer = '是否确认交卷？'
that.cancel = '再想想'
that.congfirm='立即交卷'
}







this.handIn()
that.show = true
},
onHidePopupTap(){
this.popup = false
this.radioOn = true
},
Popuphide(){
this.radioOn = true
},
onSubmitTap(){
const that = this




this.showTips()
},
onCancel () {

},
onConfirm (msg) {
const that = this
console.log(that.examInfo)
that.$router.push('../result?type='+ 2  +'&&endTimeLong=' +  that.examInfo.endTimeLong + '&&startTimeLong=' + that.examInfo.startTimeLong + '&&sorce=' + that.examInfo.sorce)
},
onHide () {
},
onShow () {
},



startPlayOrPause() {



const that = this
this.audioAdd = true
this.audioStyle = 1
that.playHelpMp3 = true
if(that.audioStyle == 2){
that.music = that.question.allMp3
if(this.audio.playing){
this.automatic = false
this.playHelpMp3 = false
}else {
this.automatic = true
}
}else {
that.music = that.question.mp3
if(this.audio.playing){
this.automatic = false
this.playHelpMp3 = false
}else {
this.automatic = true
}
}
return this.audio.playing ? this.pausePlay() : this.startPlay()
},
readTopic (){
if(!this.audio.playing){
this.audioStyle = 1
this.playHelpMp3 = true
this.music = this.question.mp3
this.audioAdd = false
return this.audio.playing ? this.pausePlay() : this.startPlay()
}
},
upgadePlayOrPause(){
this.audioStyle = 2
this.question.music = this.question.allMp3
return this.audio.playing ? this.pausePlay() : this.startPlay()
},

onError () {
this.audio.waiting = true
},

startPlay() {
this.$refs.audio.play()
},

pausePlay() {

this.$refs.audio.pause()
},

onPause () {
const that = this;

if(this.automatic){
console.log('接着下一首')











this.audio.playing = false
this.automatic = true
}else {

this.audio.playing = false
this.automatic = true
}











},

onWaiting (res) {
console.log(res)
},

onPlay (res) {
this.audio.playing = true
this.audio.loading = false
if(!this.controlList.onlyOnePlaying){
return
}
let target = res.target
let audios = document.getElementsByTagName('audio');
[...audios].forEach((item) => {
if(item !== target){
item.pause()
}
})
},

onTimeupdate(res) {
this.audio.currentTime = res.target.currentTime
this.sliderTime = parseInt(this.audio.currentTime / this.audio.maxTime * 100)
},


onLoadedmetadata(res) {
const that = this
this.audio.waiting = false
this.audio.maxTime = parseInt(res.target.duration)
if(that.nextLoading){
that.startPlay()
}else if(that.playHelpMp3){
that.startPlay()
that.playHelpMp3 = false
}
},
onSubTap(e){








if(this.radioValue.length > 0 && this.question.isRight == 0){
this.params03.optionId = this.processMultipleselection()
this.judgeQuestion()
}
}
}
}



